<template>
  <div class="home">
    <!-- <img alt="Vue logo" src="../assets/logo.png"> -->
    <!-- <HelloWorld msg="Welcome to Your Vue.js App"/> -->
    <div style="display: flex; height: 100%">
      <div class="back" style="width: 84%">
        <h1> Phonematon</h1>
        <p>Entrez votre texte ici :</p>
        <textarea v-model="test"></textarea>
        <p>Et obtenez le résultat ici :</p>
        <div style="background-color: white" id="output"></div>
      </div>
      <filterManager @execute="MakeWanted($event)" :phonemes="myJson.phonemes" style="width: 24%" />
    </div>
  </div>
</template>

<script>
// @ is an alias to /src
import filterManager from "@/components/filter/filterManager.vue";
import json from './data.json'

export default {
  name: "home",
  components: {
    filterManager
  },
  data() {
    return {
      test: "",
      myJson: json,
    };
  },
  methods: {
    MakeWanted(event) {
      this.ClearResult();
      console.log("childNodes : ", document.getElementById("output").childNodes)
      let tab = [];
      let text = JSON.parse(JSON.stringify(this.test));
      let finalText = JSON.parse(JSON.stringify(this.test));
      console.log("phonem list : ", event);
      for (let p = 0; p < event.length; p++) {
          console.log("p : ", p)
          for (let m = 0; m < event[p].madeBy.length; m++) {
            console.log("m : ", m)
            console.log(event[p].madeBy[m]);
            console.log(text);
            console.log(finalText);

            while (text.indexOf(event[p].madeBy[m].with) >= 0) {

              console.log(text);
              console.log(finalText);

              let index = text.indexOf(event[p].madeBy[m].with);
              console.log(this.TestConditions(event[p].madeBy[m], this.test, index))
              if (this.TestConditions(event[p].madeBy[m], this.test, index)) {
                console.log("passed");
                console.log(event[p]["color"])
                tab[index] = this.CreateSpan(event[p].madeBy[m].with, event[p]["color"]);
                finalText = finalText.replaceAt(index, "-");
                //finalText = finalText.replace(event[p].madeBy[m].with, "-");
              }
              text = text.replace(event[p].madeBy[m].with, "-");
              console.log("-----------------------------------------------")
          }
        }
      }
      console.log("childNodes : ", document.getElementById("output").childNodes)
      console.log(text);
      console.log(finalText);
      console.log(tab);
      this.FindAllRest(tab, finalText);
    },
    TestConditions(madeBy, text, currentIndex){
      if(Object.keys(madeBy.conditions).length > 0){

        let keys = Object.keys(madeBy.conditions);
        let stackResult = []
        for(let k = 0; k < keys.length; k++){
          let key = keys[k]
          let currentCondition = this.myJson.conditions[key]
          let result = this.TestList(madeBy.conditions[key], text, currentIndex, currentCondition.needs, currentCondition.sense)
          stackResult.push(result);
          console.log("result: "+result);
          // if(result == false){
          //   console.log("yo")
          //   return false
          // }
        };
        if(stackResult.length > 1){
          switch(madeBy.gate){
            case"or":
              return (stackResult[0] || stackResult [1] ) ? true : false
            case"and":
              return (stackResult[0] && stackResult [1] ) ? true : false
          }
        }else{
          return stackResult[0]
        }
      }
      return true
    },

    TestList(list, text, currentIndex, needs, sense){
      //console.log(list);
      for(let i = 0; i < list.length; i++){
          let p = list[i]
          // Pour tout les son de la liste
          // si le(s) lettre(s) qui suivent la lettre font partie du son alors
          console.log("needs: " + needs)
          console.log("sense: " + sense)
          // console.log("pLength: " + p.length)
          console.log(text.substr(currentIndex - (sense ? -1 : p.length), p.length))
          console.log(p)
          if(text.substr(currentIndex - (sense ? -1 : p.length), p.length) == p){
            console.log("ya")
            return needs
          }
      }
      return list.length > 0 ? !needs:needs;
    },
    FindAllRest(tab, text) {
      let nextWord = "";
      let currentIndex = 0;
      for (let l = 0; l < text.length; l++) {
        if (text[l] == "-") {
          if (nextWord.length > 0) {
            tab[currentIndex] = this.CreateSpan(nextWord, "000000");
          }
          currentIndex = l + 1;
          nextWord = "";
        } else {
          nextWord += text[l];
        }
      }
      if (nextWord.length > 0) {
        tab[currentIndex] = this.CreateSpan(nextWord, "000000");
      }
      this.FillResult(tab);
    },
    ClearResult() {
      let doc = document.getElementById("output");
      if (doc.hasChildNodes()) {
        var child = doc.lastElementChild;
        while (child) {
          doc.removeChild(child);
          child = doc.lastElementChild;
        }
      }
      console.log("childNodes : ", doc.childNodes)
    },
    FillResult(tab) {
      console.warn("Fill Result")
      console.log("childNodes : ", document.getElementById("output").childNodes)
      console.log("tab : ", tab)
      for (let i = 0; i < tab.length; i++) {
        if (tab[i] != undefined) {
          document.getElementById("output").appendChild(tab[i]);
        }
      }
    },
    CreateSpan(text, color) {
      var span = document.createElement("span");
      span.innerText = text;
      span.style.color = color
      return span;
    }
  },
  mounted() {
    let aie = document.getElementById("output");
    console.log(aie);
  }
};
</script>
