<template>
  <div
    style="background-color: #f2f2f2; display: flex; flex-direction: column; justify-content: space-between"
  >
    <div>
      <div class="title">
        <h1 class="TitleShadow">FILTRE</h1>
      </div>
      <div class="lister">
        <item
          @askChangeDropDown="askDropdownChange($event, index)"
          @addItem="AddItem(itemInfos, index)"
          @removeItem="RemoveItem(index)"
          @Hey="Hey($event, index)"
          :itemConfig="itemInfos"
          v-for="(itemInfos,index) in itemList"
          :key="itemInfos.id"
        />
      </div>
    </div>
    <button @click="Validate" class="validateButton">
      <h1 class="TitleButtonShadow">APPPLY</h1>
    </button>
  </div>
</template>
    
<script>
import item from "@/components/filter/item.vue";

export default {
  name: "filterManager",
  components: {
    item
  },
  props: {
    phonemes: {
      type: Array,
      required: true
    }
  },
  data() {
    return {
      itemList: [],
      //itemList: [],
      selectedPhonemId: [],
      currentIndex: 0
    };
  },
  methods: {
    askDropdownChange(object, index) {
      let currentItem = this.itemList[index];
      if (currentItem.currentlyUsed > 1) {
        let selectedId = currentItem.dropDownConfig.selectedId;
        this.itemListUseChecker(-1, selectedId, index);
      }
      currentItem.dropDownConfig.selectedId = object.option.id;
      currentItem.dropDownConfig.selectedIndex = object.index;
      this.itemListSameCounter(object.option.id);
    },
    itemListUseChecker(value, idToSearch, indexToSkip) {
      for (let i = 0; i < this.itemList.length - 1; i++) {
        let element = this.itemList[i];
        if (
          i != indexToSkip &&
          element.dropDownConfig.selectedId == idToSearch
        ) {
          element.currentlyUsed += value;
        }
      }
    },
    itemListSameCounter(idToSearch) {
      let indexList = [];
      for (let i = 0; i < this.itemList.length - 1; i++) {
        let element = this.itemList[i];
        if (element.dropDownConfig.selectedId == idToSearch) {
          indexList.push(i);
        }
      }
      indexList.forEach(element => {
        this.itemList[element].currentlyUsed = indexList.length;
      });
    },
    AddItem(item, currentIndex) {
      if (this.itemList.length < this.phonemes.length + 1) {
        this.itemList[currentIndex].active = true;
        this.itemList.push({
          id: currentIndex + 1,
          currentlyUsed: 1,
          active: false,
          color: "#FF0000",
          dropDownConfig: {
            options: this.phonemes,
            selectedIndex: 0,
            selectedId: 0,
          }
        });
        this.itemListSameCounter(0);
      }
      return true;
    },
    RemoveItem(index) {
      this.itemList.splice(index, 1);
    },
    Validate() {
      let indexList = [];
      let phonemList = [];
      for (let i = 0; i < this.itemList.length - 1; i++) {
        let element = this.itemList[i];
        if (indexList.indexOf(element.dropDownConfig.selectedId) == -1) {
          indexList.push(element.dropDownConfig.selectedId);
          phonemList.push(this.phonemes[element.dropDownConfig.selectedIndex]);
          phonemList[phonemList.length - 1]["color"] = this.itemList[phonemList.length-1]["color"]
        }
      }
      this.$emit("execute", phonemList);
    },
    Hey(event, index){
      this.itemList[index].color = event.target.value
    }
  },
  mounted() {
    this.itemList.push({
      id: 0,
      currentlyUsed: 1,
      active: false,
      color: "",
      dropDownConfig: {
        options: this.phonemes,
        selectedIndex: 0,
        selectedId: 0
      }
    });
    console.log(this.itemList[0]);
  }
};
</script>

